@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-content{
    background: transparent;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;
    height: 58px;
    padding: 0 1.5rem;
}



.nav-items{
    display: none;
}

.nav-items a {
    padding: 0;
    color: rgba(15, 15, 15, 0.75);
    font-size: .875rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    text-transform: uppercase;
    margin: 0 1rem 0 0;
    transition: color .25s;
}

.nav-items a:hover {
    color: #d88a83 !important;
    cursor: pointer;
}

.active{
    color: #d88a83 !important;
}

.Brand{
    display: flex;
    align-items: center;
}

.Brand-logo, .Brand-name{
    margin: 0 .7rem 0 0;
    padding: 0;
}

.Brand-logo{
    width: 32px;
    height: 32px;
    border-radius: 50%;
}

.Brand-name{
    font-size: .875rem;
    font-family: "Poppins";
    font-weight: 800;
    transition: color .25s;
}

@media (min-width: 768px){
    .nav-content{
        align-items: center;
    }
    .nav-items{
        display: flex;
        align-items: center;
    }

    .Brand{
       align-items: center;
    }

    .Brand-logo{
        height: 38px;
        width: 38px;
    }
    .Brand-logo, .Brand-name{
        margin: 0 1rem 0 0;
        padding: 0;
    }
}
.home-wrapper{
    overflow: hidden;
    min-height: 100vh;
    position: relative;
    display: flex;
    align-items: flex-start;
}

.home-left{
    background: #d88a83;
    min-height:  100vh;
    width: 50%;
}

.home-banner{
    position: absolute;
    top: 50%;
    left: calc(50% - 5px);
    width: 50%;
    transform: translate(-50%,-50%) scale(1);
}

.scroll-down-link-wrapper{
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-animation: scroll-down-animation 1.375s infinite alternate;
            animation: scroll-down-animation 1.375s infinite alternate;
}

@-webkit-keyframes scroll-down-animation {
    0% { transform: translateY(0); }
    100% { transform: translateY(-30px); }
}

@keyframes scroll-down-animation {
    0% { transform: translateY(0); }
    100% { transform: translateY(-30px); }
}

.scroll-down-link{
    display: inline-block;
    color: #d88a83 !important;
    padding: 1rem 1rem 4rem 1rem;
    cursor: pointer;
}

.scroll-down-link-inner{
    display: flex;
    align-items: center;
}

.home-scroll-down-text{
    font-size: 1rem;
    font-weight: 600;
    margin-left: .325rem;
}

@media (min-width: 768px){
.home-scroll-down-text {
    font-size: 1.25rem;
}

.scroll-down-link{
    padding: 1rem;
}
}
.about-wrapper{
    min-height: 100vh;
    position: relative;
}

.swipe-carousel{
    display: flex;
    flex-direction: column;
    position: relative;
}

.carousel-wrapper{
    width: 100%;
    position: relative;
    overflow: hidden;
    padding: 3rem 0;
}

.about-center{
   padding-top: 10rem;
}

.right-styled{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 3rem;
}

.avatar-wrapper{
    border-radius: 50%;
    width: 40%;
}

.avatar{
    margin: 0;
    padding: 0;
    width: 100%;
    border-radius: 50%;
}

.about-title{
    position: relative;
    margin-bottom: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #0f0f0f;
}

.about-title-heading{
    font-size: 2.25rem;
    font-weight: 800;
    text-align: center;
    margin-bottom: 1rem;
}

.about-title-subtitle{
    font-size: .875rem;
    font-weight: 800;
    color:rgba(0,0,0,.57);
    margin-bottom: 1.25rem;
    text-align: center;
}

.about-description{
    font-size: .975rem;
    font-weight: 600;
    color: rgba(0,0,0,.5);
    line-height: 1.625;
    text-align: center;
    margin-bottom: 2rem;
}

.swipe-wrapper{
    display: flex;
    justify-content: center; 
}

.swipe-button{
    cursor: move;
    background: 0 0;
    display: flex;
    align-items: center;
    -webkit-animation: swipe-animation 1.375s infinite alternate;
            animation: swipe-animation 1.375s infinite alternate;
}

@-webkit-keyframes swipe-animation {
    0% { transform: translateX(0); }
    100% { transform: translateX(-30px); }
}

@keyframes swipe-animation {
    0% { transform: translateX(0); }
    100% { transform: translateX(-30px); }
}

.swipe-text{
    margin-left: .625rem;
    font-size: 1.25rem;
    font-weight: 600;
    color: #d88a83;
}
.about-title-border{
    width: 65px;
    height: 3px;
    background: #d88a83;
}

.about-heading{
    font-size: 1.5rem;
    font-weight: 800;
    margin-bottom: 2.5rem;
}

.passions-row{
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
}

.about-swiped-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0;
}

.skills-subtitle{
    font-size: .875rem;
    font-weight: 800;
    color: #d88a83;
    margin-bottom: .75rem;
    text-align: center;
}

.skills-list{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 60%;
    margin: 0 auto 2rem;
}

.skills-list-item{
    height: 40px;
    font-size: .975rem;
    font-weight: 500;
    color: rgba(0,0,0,.65);
    padding: .5rem .25rem 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(0,0,0,.125);
    margin-right: .5rem;
}

.language-block, .language-wrapper{
    display: flex;
}

.language-wrapper{
    align-items: center;
    margin-bottom: 2rem;
}

.language-block{
    flex-direction: column;
    width: 130px;
}

.language-type{
    font-size: .9rem;
    font-weight: 600;
    color: rgba(0,0,0,.57);
    margin-bottom: .325rem;
}

.language-accuracy{
    font-size: .75rem;
    font-weight: 600;
    color: hsla(0,0%,50.2%,.57);
}

.progress-wrapper{
    flex: 1 1;
    background: #e0e0e0;
    border-radius: 10px;
    width: 130px;
}

.progressBar{
    height: .5rem;
    border-radius: .5rem;
    background: #d88a83
}

.slide-2-mobile, .slide-3-mobile{
    display: flex;
}


@media (min-width: 420px)
{
    .about-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
}
}

@media (min-width: 768px){
    .about-title{
        align-items: flex-start;
    }
    .about-title-heading, .about-description{
        text-align: left;
    }

    .swipe-wrapper{
        justify-content: flex-start;
    }

    .swipe-carousel{
        padding: 8rem 0;
        cursor: move;
    }

    .right-styled{
        text-align: left;
        margin-bottom: 0;
        align-items: center;
    }

    .avatar-wrapper{
        width: 200px;
        height: 200px;
    
    }
    .about-swiped-wrapper{
        align-items: flex-start;
        padding: 0;
    }

    .skills-list {
        justify-content: flex-start;
        width: 100%;
    }

    .skills-list-item{
        margin-right: .5rem;
    }

    .skills-subtitle{
        text-align: start;
    }

    .slide-2-mobile, .slide-3-mobile{
        display: none;
    }
}


.passion-type{
    color: rgba(0,0,0,.57);
    font-size: .875rem;
    font-weight: 600;
    margin-left: .75rem;
}




.portfolio-wrapper{
    padding: 7rem 0;
    background: #f2f2f2;
}

.portfolio-wrapper .container .row{
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}


.portfolio-title-wrapper{
    position: relative;
    margin-bottom: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #0f0f0f;
}

.portfolio-title{
    font-size: 2.25rem;
    font-weight: 800;
    text-align: center;
    margin-bottom: .325rem;
}

.portfolio-subtitle{
    font-size: 1.175rem;
    font-weight: 800;
    color: rgba(0,0,0,.29);
    text-align: center;
}

.portfolio-row{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-img-wrapper {
    width: 60%;
    margin-bottom: 3rem;
}

.project-img{
    width: 100%;
    border-radius: .5rem;
    box-shadow: 2px 2px 50px rgba(0,0,0,.15);
    transition: transform .5s;
}

.project-description{
    flex: 1 1 auto;
    text-align: center;
    width: 60%;
}

.project-name{
    margin-bottom: .5rem;
    font-size: 1.25rem;
    font-weight: 800;
    color: #0f0f0f;
}

.project-description{
    flex: 1 1 auto;
    text-align: center;
    width: 60%;
    
}

.project-description-text{
    color: rgba(0,0,0,.65);
    line-height: 1.625;
    font-size: .875rem;
    margin-bottom: 1rem;
}

.project-video {
    color: #d88a83;
    font-weight: 700;
    font-size: 1rem;
    text-decoration: none;
}

.project-video:hover{
    color:#d88a83;
    text-decoration: none;
}

@media (min-width: 992px){
    .portfolio-row{
        flex-direction: row;
    }
    
    .project-img-wrapper{
        margin-bottom: 0;
        flex: 1 1 auto;
        width: 55%;
    }

    .project-description{
        width: 45%;
        text-align: left;
    }
    
    .project-description-right{
        padding-left: 60px;
    }

    .project-description-left{
        padding-right: 60px;
    }

    .row-reverse{
        flex-direction: row-reverse;
    }
    
    }

