.footer-wrapper{
    position: relative;
    overflow: hidden;
    padding: 5rem 0;
    background: #171717;
}

.footer-header-title{
    color: #fff;
    font-size: 1.25rem;
    font-weight: 700;
    margin-bottom: 1rem;
    text-align: center;
}

.footer-content{
    font-size: 14px;
    line-height: 1.35;
    color: hsla(0,0%,100%,.5);
    margin-bottom:  2rem;
    text-align: center;
}

.footer-link{
    display: block;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.35;
    text-decoration: none;
    color: hsla(0,0%,100%,.5) !important;
    margin-bottom: .5rem;
    transition: color .35s;
    cursor: pointer;
}

.footer-icon-list{
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-icon{
    width: 34px;
    height: 34px;
    border-radius: 50%;
    cursor: pointer;
    justify-content: center;
    margin-right: 0.4rem;
}

@media (min-width: 768px){
    .footer-header-title{
        text-align: left;
    }

    .footer-content{
        width: auto;
        margin-bottom: 0;
        text-align: left;
      }

      .footer-icon-list{
          justify-content: flex-start;
      }
}
.loader-outer{
    position: fixed;
    z-index: 20;
    height: 100vh;
    width: 100%;
    background: #000;
    display: flex;
    justify-content: space-around;
    transition-property: opacity, visibility;
    transition-duration: 1s;
    /* animation: 1s fadeIn; */
}

.loader-outer.loaded {
    opacity: 0;
    visibility: hidden;
}

.loader {
    border: 8px solid #2b2929;
    border-radius: 50%;
    border-left-color:#d88a83;
    margin: auto;
    width: 100px;
    height: 100px;
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 1s linear infinite;
  }

  /* Safari */
@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
